import React, { useEffect, useState } from "react";
import Modal from "react-modal";
import moment from "moment";
import DateTimePicker from "react-datetime-picker";
import Swal from "sweetalert2";
import { useDispatch, useSelector } from "react-redux";
import { uiCloseModal } from "../../actions/ui";
import {
  eventAddNew,
  eventCleanActiveEvent,
  eventStartAddNew,
  eventStartUpdated,
  eventUpdated,
} from "../../actions/events";

const customStyles = {
  content: {
    top: "50%",
    left: "50%",
    right: "auto",
    bottom: "auto",
    marginRight: "-50%",
    transform: "translate(-50%, -50%)",
  },
};

Modal.setAppElement("#root");
const now = moment().minutes(0).seconds(0).add(1, "hours");
// const dateend = now.add(1,'hours')
const dateend = now.clone(1, "hours");

const initEvent = {
  title: "",
  notes: "",
  start: now.toDate(),
  end: dateend.toDate(),
};

const CalendarModal = () => {
  // const [isOpen, setisOpen] = useState(true)
  const [dateStart, setdateStart] = useState(now.toDate());
  const [dateEnd, setdateEnd] = useState(dateend.toDate());
  const [titleValid, settitleValid] = useState(true);
  const [formValue, setFormValue] = useState(initEvent);
  const { notes, title, start, end } = formValue;
  const { modalOpen } = useSelector((state) => state.ui);
  const { activeEvent, events } = useSelector((state) => state.calendar);
  const dispatch = useDispatch();

  useEffect(() => {
    if (activeEvent) {
      setFormValue(activeEvent);
    }else{
      setFormValue(initEvent);
    }
  }, [activeEvent, setFormValue]);

  const onchange = ({ target }) => {
    setFormValue({
      ...formValue,
      [target.name]: target.value,
    });
  };

  const closeModal = () => {
    // setisOpen(false) cerrr modal
    dispatch(uiCloseModal());
    dispatch(eventCleanActiveEvent());
    setFormValue(initEvent);
  };

  const handleStartDateChange = (e) => {
    // setisOpen(false)
    // console.log({ e });
    setdateStart(e);
    setFormValue({
      ...formValue,
      start: e,
    });
  };
  const handleEndtDateChange = (e) => {
    // setisOpen(false)
    // console.log({ e });
    setdateEnd(e);
    setFormValue({
      ...formValue,
      end: e,
    });
  };

  const submit = (e) => {
    e.preventDefault();
    const momentStart = moment(start);
    const momentEnd = moment(end);
    if (momentStart.isSameOrAfter(momentEnd)) {
      return Swal.fire(
        "Error",
        "La fecha fin debe ser mayor a la fecha de inicio",
        "error"
      );
    }

    if (title.trim().length < 2) {
      // return Swal.fire('Error', 'La fecha fin debe ser mayor a la fecha de inicio', 'error'
      settitleValid(false);
    }

    // Realizar grabacion en bd
    if (activeEvent) {
      dispatch(eventStartUpdated(formValue));
    } else {
      dispatch(
        eventStartAddNew({
          ...formValue
        })
      );
    }
    settitleValid(true);
    closeModal();
    // console.log({formValue});
  };

  return (
    <Modal
      className="modal"
      overlayClassName="modal-fondo"
      closeTimeoutMS={200}
      isOpen={modalOpen}
      //   onAfterOpen={afterOpenModal}
      onRequestClose={closeModal}
      style={customStyles}
    >
      <h1>{ activeEvent ? 'Editar Evento': 'Nuevo evento'} </h1>
      <hr />
      <form onSubmit={submit} className="container">
        <div className="form-group">
          <label>Fecha y hora inicio</label>
          <DateTimePicker
            className="form-control"
            onChange={handleStartDateChange}
            value={dateStart}
          />
        </div>

        <div className="form-group">
          <label>Fecha y hora fin</label>
          <DateTimePicker
            minDate={dateStart}
            className="form-control"
            onChange={handleEndtDateChange}
            value={dateEnd}
          />
        </div>
        <hr />
        <div className="form-group">
          <label>Titulo y notas</label>
          <input
            type="text"
            className={`form-control ${
              !titleValid ? "is-invalid" : "is-valid"
            }`}
            placeholder="Título del evento"
            name="title"
            autoComplete="off"
            value={title}
            onChange={onchange}
          />
          <small id="emailHelp" className="form-text text-muted">
            Una descripción corta
          </small>
        </div>

        <div className="form-group">
          <textarea
            type="text"
            className="form-control"
            placeholder="Notas"
            rows="5"
            name="notes"
            value={notes}
            onChange={onchange}
          ></textarea>
          <small id="emailHelp" className="form-text text-muted">
            Información adicional
          </small>
        </div>

        <button type="submit" className="btn btn-outline-primary btn-block">
          <i className="far fa-save"></i>
          <span> Guardar</span>
        </button>
      </form>
    </Modal>
  );
};

export default CalendarModal;
