import React, { useState } from "react";
import Navbar from "../ui/Navbar";
import { Calendar, momentLocalizer } from "react-big-calendar";
import moment from "moment";
import "react-big-calendar/lib/css/react-big-calendar.css";
import { messages } from "../../helpers/calendarMessages";
import "moment/locale/es";
import CalendarEvent from "./CalendarEvent";
import CalendarModal from "./CalendarModal";
import { useDispatch, useSelector } from "react-redux";
import { uiOpenModal } from "../../actions/ui";
import { eventCleanActiveEvent, eventSetActive, eventStartLoading } from "../../actions/events";
import AddNewFab from "../ui/AddNewFab";
import DeleteEventFab from "../ui/DeleteEventFab";
import { useEffect } from "react";
moment.locale("es");

const localizer = momentLocalizer(moment);
// const events = [
//   {
//     title: "Cumple el jefe",
//     start: moment().toDate(),
//     end: moment().add(2, "hour").toDate(),
//     bgcolor: "#fafafa",
//     notes: "comprar torta",
//     user: {
//       _id: "1234",
//       name: "Adrian",
//     },
//   },
// ];



const now = moment().minutes(0).seconds(0).add(1, "hours");
// const dateend = now.add(1,'hours')
const dateend = now.clone(1, "hours");

const initEvent = {
  title: "",
  notes: "",
  start: now.toDate(),
  end: dateend.toDate(),
};

const CalendarScreen = () => {
  const [lastView, setLastView] = useState(
    localStorage.getItem("lastView") || "month"
  );
  const { modalOpen } = useSelector((state) => state.ui);
  const { uid } = useSelector((state) => state.auth);
  const dispatch = useDispatch();
  const { activeEvent, events } = useSelector((state) => state.calendar);

  // console.log({activeEvent, events })
  useEffect(() => {
    dispatch(eventStartLoading())
  }, [dispatch]);

  const onDobleClick = (e) => {
    // console.log({ e });
    dispatch(uiOpenModal());
  };

  const onSelect = (e) => {
    // console.log({ e });
    dispatch(eventSetActive(e));
  };

  const SelectSlot = (e) => {
    console.log({ e });
    dispatch(uiOpenModal());
    dispatch(eventCleanActiveEvent());

  };

  const onViewChange = (e) => {
    console.log({ e });
    setLastView(e);
    localStorage.setItem("lastView", e);
  };

  const eventStyleGetter = (event, start, end, isSelected) => {
    // console.log({ event, start, end, isSelected });
    const style = {
      backgroundColor: (uid === event.user._id) ? '#367CF7': "#465660",
      borderRadius: "0px",
      opacity: 0.8,
      display: "block",
      color: "white",
    };
    return {
      style,
    };
  };

  return (
    <div className="calendarScreen">
      <Navbar />

      <Calendar
        localizer={localizer}
        events={events}
        startAccessor="start"
        endAccessor="end"
        messages={messages}
        eventPropGetter={eventStyleGetter}
        components={{ event: CalendarEvent }}
        onDoubleClickEvent={onDobleClick}
        onSelectEvent={onSelect}
        onView={onViewChange}
        view={lastView}
        onSelectSlot={SelectSlot}
        selectable={true}
      />
      <AddNewFab />
      {activeEvent ? <DeleteEventFab /> : null}
      <CalendarModal />
    </div>
  );
};

export default CalendarScreen;
